<template>
  <div>
    <join-header :maintitle="mainTitle"></join-header>
    <join-top></join-top>
    <join-main></join-main>
    <join-policy></join-policy>
    <footer-component></footer-component>
    <div class="topBtn" v-on:click="topBtn">금융기관 모니터링 신청</div>
  </div>
</template>

<script>
import JoinHeader from '@/components/Join/JoinHeader.vue';
import FooterComponent from '@/components/FooterComponent.vue';

import JoinTop from '@/components/Join/JoinTop.vue';
import JoinMain from '@/components/Join/JoinMain.vue';
import JoinPolicy from '@/components/Join/JoinPolicy.vue';

export default {
	data() {
		return {
			mainTitle: 'PASS 세이프가드 가입',
		};
	},
	components: {
		JoinHeader,
		FooterComponent,
		JoinTop,
		JoinMain,
		JoinPolicy,
	},
	methods: {
		topBtn() {
			document.documentElement.scrollTop = 0;
		},
	},
};

window.onscroll = function() {
	var sclV = window.scrollY;
	if (sclV > 10) {
		document.querySelector('.topBtn').classList.add('fixed');
	} else {
		document.querySelector('.topBtn').classList.remove('fixed');
	}
};
</script>

<style scoped>
.topBtn {
	position: fixed;
	bottom: 0;
	width: 100%;
	left: 0;
	background-color: #ff3782;
	height: 0px;
	overflow: hidden;
	text-align: center;
	color: #fff;
	line-height: 65px;
	font-size: 20px;
	font-weight: bold;
	transition: all 0.5s;
	z-index: 9999;
}
.topBtn.fixed {
	height: 65px;
}
footer {
	margin-bottom: 65px;
}
</style>