<template>
  <div class="joinPolicy">
    <form action v-on:submit="checkForm">
      <div class="inputBox">
        <div class="leftBox">
          <b>SKT</b>
        </div>
        <div class="rightBox">
          <input type="text" maxlength="11" placeholder="01012345678('-'제외)" v-model="phoneNumber" />
        </div>
      </div>

      <div class="checkBox">
        <div class="allAgree" v-on:click="policyAllActive">
          <span class="chkAll chkBox" :class="{ active : policyAll }"></span>
          <p>모두 동의</p>
        </div>

        <ul>
          <li>
            <span class="chkBox" :class="{ active : policy1 }" v-on:click="policyActive(1)"></span>
            <p v-on:click="popupOpen">서비스 이용약관(필수)</p>
          </li>
          <li>
            <span class="chkBox" :class="{ active : policy2 }" v-on:click="policyActive(2)"></span>
            <p>개인정보수집 및 이용(필수)</p>
          </li>
          <li>
            <span class="chkBox" :class="{ active : policy3 }" v-on:click="policyActive(3)"></span>
            <p>개인정보 제3자 제공(필수)</p>
          </li>
          <li>
            <span class="chkBox" :class="{ active : policy4 }" v-on:click="policyActive(4)"></span>
            <p>금융거래알림푸시수신(필수)</p>
          </li>
        </ul>
      </div>

      <div class="btnArea">
        <button type="submit">PASS 세이프가드 가입</button>
        <span>매월 부가세포함 2,200원이 휴대전화 요금에 합산 청구됩니다.</span>
      </div>
    </form>

    <step-desc></step-desc>
    <policy-popup v-if="popupActive" v-on:closepopup="popupClose"></policy-popup>
  </div>
</template>

<script>
import StepDesc from '@/components/Join/StepDesc.vue';
import PolicyPopup from '@/components/Join/PolicyPopup.vue';
export default {
	data() {
		return {
			phoneNumber: '',
			policyAll: false,
			policy1: false,
			policy2: false,
			policy3: false,
			policy4: false,
			popupActive: false,
			policyCheck: 0,
		};
	},
	components: {
		StepDesc,
		PolicyPopup,
	},
	watch: {
		policyCheck: function(val) {
			if (val == 4) {
				this.policyAll = true;
			} else if (val < 4) {
				this.policyAll = false;
			}
		},
	},
	methods: {
		popupOpen() {
			this.popupActive = true;
		},
		popupClose() {
			this.popupActive = false;
		},
		policyAllActive() {
			this.policyAll = !this.policyAll;
			if (this.policyAll == true) {
				this.policy1 = true;
				this.policy2 = true;
				this.policy3 = true;
				this.policy4 = true;
			} else {
				this.policy1 = false;
				this.policy2 = false;
				this.policy3 = false;
				this.policy4 = false;
			}
		},
		policyActive(index) {
			switch (index) {
				case 1:
					if (this.policy1 == true) {
						this.policyCheck -= 1;
					} else if (this.policy1 == false) {
						this.policyCheck += 1;
					}
					this.policy1 = !this.policy1;
					break;
				case 2:
					if (this.policy2 == true) {
						this.policyCheck -= 1;
					} else if (this.policy2 == false) {
						this.policyCheck += 1;
					}
					this.policy2 = !this.policy2;
					break;
				case 3:
					if (this.policy3 == true) {
						this.policyCheck -= 1;
					} else if (this.policy3 == false) {
						this.policyCheck += 1;
					}
					this.policy3 = !this.policy3;
					break;
				case 4:
					if (this.policy4 == true) {
						this.policyCheck -= 1;
					} else if (this.policy4 == false) {
						this.policyCheck += 1;
					}
					this.policy4 = !this.policy4;
					break;
			}
		},
		checkForm() {
			if (this.phoneNumber.length <= 10) {
				alert(
					'본인의 전화번호를 정확히 입력하셨는지 다시 한번 확인부탁드립니다.',
				);
				return false;
			}

			let numberChk = this.phoneNumber.substring(0, 3);

			if (
				numberChk == '010' ||
				numberChk == '011' ||
				numberChk == '016' ||
				numberChk == '017' ||
				numberChk == '018' ||
				numberChk == '019'
			) {
			} else {
				alert(
					'본인의 전화번호를 정확히 입력하셨는지 다시 한번 확인 부탁드립니다.',
				);
				return false;
			}

			if (this.policy1 && this.policy2 && this.policy3 && this.policy4) {
			} else {
				alert('서비스 이용약관 등을 모두 동의하셔야 서비스 가입이 가능합니다.');
				return false;
			}
		},
	},
};
</script>

